import React from "react";
import { NetlifySDKProvider } from "../NetlifySDKProvider.jsx";
import { Theme } from "../theme/Theme.jsx";
import { useWindowResizer } from "../use_window_resizer.js";
import { FullstorySetup } from "./FullstorySetup.jsx";
import {
  LinkProvider,
  type LinkProviderRenderLink,
} from "./Link/LinkProvider.jsx";
import { defaultImportSvg, SvgProvider } from "./SvgProvider.jsx";

type ResizerProps = {
  children?: React.ReactNode | undefined;
};

const Resizer = ({ children }: ResizerProps) => {
  useWindowResizer();

  return <>{children}</>;
};

const renderLink: LinkProviderRenderLink = ({ href, ...otherProps }) => (
  <a {...otherProps} href={href} rel="noopener noreferrer" target="_blank" />
);

export type NetlifyExtensionUIProps = {
  children?: React.ReactNode | undefined;
};

/**
 * <!-- TODO(ndhoule): Document me -->
 *
 * @internal
 */
export const NetlifyExtensionUI = ({ children }: NetlifyExtensionUIProps) => {
  // TODO(ndhoule): Enable React.StrictMode
  // TODO(ndhoule): Add an error boundary
  // TODO(ndhoule): Optionally: Connect error boundary to Netlify-owned error system (e.g. Bugsnag)
  // https://react.dev/reference/react/Component#catching-rendering-errors-with-an-error-boundary
  return (
    <NetlifySDKProvider>
      <Resizer>
        <SvgProvider importSvg={defaultImportSvg}>
          <LinkProvider renderLink={renderLink}>
            <Theme />
            <FullstorySetup />
            {children}
          </LinkProvider>
        </SvgProvider>
      </Resizer>
    </NetlifySDKProvider>
  );
};
